  display: flex;
  flex-direction: column;
  justify-content: center;
  line-height: 2rem;
  text-align: left;
}

.job-card-details {
  margin-block: 1.2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.job-details-condition {
  width: 8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.8rem;
}

.job-details-headings {
  display: flex;
  justify-content: space-evenly;
  -webkit-margin-after: 2%;
          margin-block-end: 2%;
  font-size: 0.7rem;
  width: 100%;
}

.job-details-icon {
  color: #fff;
  background-color: #187271;
  border-radius: 50%;
  padding: 1%;
  font-size: 1rem;
}

/* filter */

.filter-card {
  width: 85%;
  height: 25rem;
  padding: 8%;
  text-align: center;
}

.filter-heading {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* cart */
.screenshot {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 400px) {
  .jobTitle {
    font-size: 1.5rem !important;
    padding-top: 20px !important;
  }
  .jobLinks {
    padding: 0.2rem 0 0 0 !important;
    flex-direction: column !important;
  }
  .job-card{
    height: 33%;
    padding:4%;
  }
}

@media (max-width: 600px) {
  .resCart {
    padding-bottom: 100px;
  }
  .resCartdiv {
    padding: 1%;
  }
}

